.mainbox {
    background-color: white;
    height : inherit;
	margin:10%;

	padding-bottom: 5%;
	margin-top: 6%;

}


.myButton {
	
	background-color:#163948;
	border-radius:5px;
	border:1px solid #d6bcd6;
	display:inline-block;
	cursor:pointer;
	color:white;
	font-family:Arial;
	font-size:15px;
	padding:5px 15px;
	text-decoration:none;
	text-shadow:0px 1px 0px #e1e2ed;
}
.myButton:hover {

	background-color:#DDADA6;
	color: white;
	text-decoration: none;
}
.myButton:active {
	position:relative;
	top:1px;
}

.myButton2 {
	
    background-color:#163948;
    height:35px;
    width:100px;
	border-radius:5px;
	border:1px solid #d6bcd6;
	display:inline-block;
	cursor:pointer;
	color:white;
	font-family:Arial;
	font-size:13px;
	padding:4px 10px;
	text-decoration:none;
	text-shadow:0px 1px 0px #e1e2ed;
}
.myButton2:hover {

	background-color:#DDADA6;
	color: white;
	text-decoration: none;
}
.myButton2:active {
	position:relative;
	top:1px;
}

.customers {
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
.customers td {
	text-align: left;
	
}
.customers td, .customers th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
.customers tr:nth-child(even){background-color: #f2f2f2;}
  
.customers tr:hover {background-color: #ddd;}
  
.customers th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #163948;
    color: white;
  }
